<template>
  <div class="shopSuitSearch">
    <nav-header :leftArrow="!$route.query.browerFirstIn">
      <div class="share-btn" v-if="shareShow"  slot="right" :style="{backgroundImage: `url(${$globalIcon.blackShareIcon})`}" @click="doShare"></div>
    </nav-header>
    <div style="height: .92rem" />
    <van-search v-model="searchText" :show-action="isSearch" shape="round" placeholder="输入成套方案名称或商品名称" @clear="clear" ref="search" @focus="searchFocus" :class="{'search-fixed':isSearch}">
      <template #left v-if="isSearch">
        <div class="shopSuitSearch-search-left">
          <van-icon name="arrow-left" @click="back" />
        </div>
      </template>
      <template #action v-if="isSearch">
        <div @click="onSearch">搜索</div>
      </template>
    </van-search>
    <img class="top-img" :src="shopInfo.titlePic" v-if="shopInfo.titlePic && !isSearch" />
    <van-list class="shopSuitSearch-list" v-model="loading" :finished="finished" finishedText="~暂无更多数据~" @load="nextPage" van-clearfix :immediate-check="false">
      <div style="height:.4rem;" v-if="isSearch"></div>
      <template v-if="showEmpty">
        <div class="shopSuitSearch-empty">
          <img :src="`${$cdnImageUrl}/search/noWord.png`" />
          <span>换个词试一下</span>
        </div>
      </template>
      <div class="suit-item" v-for="(item,index) in suitList" :key="index" @click="goDetail(item)">
        <div class="img-row">
          <img v-for="(pic,index) in item.detailList" :key="index" class="img-item" :src="pic.picUrl" />
        </div>
        <span class="title">
          {{item.packageName}}
        </span>
        <div class="price-row">
          <div class="price-box">
            <div class="price">
              <span class="symbol">￥</span>
              <span class="price-int">{{item.totalPackagePrice | formatPrice | formatPriceInt}}</span>
              <span class="price-float">{{item.totalPackagePrice | formatPrice | formatPriceFloat}}</span>
            </div>
            <img class="price-img" src="//cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/shop/icon_pice_tag.png">
          </div>
          <div class="origin-price">
            <span class="tag">原价总计:</span>
            <span class="symbol">￥</span>
            <span class="price-int">{{item.totalStorePrice | formatPrice | formatPriceInt}}</span>
            <span class="price-float">{{item.totalStorePrice | formatPrice | formatPriceFloat}}</span>
          </div>
        </div>
      </div>
    </van-list>
  </div>
</template>
<script>
import NavHeader from '@/components/header';
import { getSuitList, getSuitImage } from '@/http/service/shop';
import config from '@/config/index';
export default {
  name: 'shopSuilSearch',
  components: {
    NavHeader
  },
  data () {
    return {
      pageNo: 1,
      pageSize: 20,
      finished: false,
      loading: false,
      suitList: [],
      searchText: '',
      showEmpty: false,
      isSearch: false,
      shareShow: this.$util.isSgRn() || this.$util.isHaierApp(),
      shopInfo: {
        titlePic: '',
      }
    }
  },
  computed: {
    storeId () {
      return this.$route.query.storeId
    },
    mId () {
      return localStorage.getItem('mId')
    }
  },
  mounted () {
    this.getSuitImage()
    this.$globalLoading.open() // 开启loading
    this.getSuitList('', 1).finally(() => {
      this.$globalLoading.close() // 关闭loading
    })
    // this.$nextTick(() => {
    //   let me = this
    //   document.body.addEventListener('touchmove', function (e) {
    //     if (!me.suitList.length || me.showEmpty) {
    //       e.preventDefault();
    //     }
    //     // 阻止默认的处理方式(阻止下拉滑动的效果)
    //   }, { passive: false });
    // })
    this.WeChatSecondShare()
  },
  methods: {
    getSuitList (keyword, pageNo) {
      this.pageNo = pageNo
      const param = {
        pageNo: pageNo,
        pageSize: this.pageSize,
        storeId: this.storeId,
        keyword
      };
      return getSuitList(param).then(res => {
        if (res.success) {
          this.showEmpty = false
          if (res.data && res.data.length > 0) {
            if (this.pageNo === 1) {
              this.suitList = res.data
            } else {
              this.suitList = this.suitList.concat(res.data);
            }
          } else {
            if (this.pageNo === 1) {
              this.suitList = []
              this.showEmpty = true
            } else {
              this.finished = true;
            }
          }
        } else {
          this.suitList = []
          this.showEmpty = true
          this.finished = true;
        }
      }).finally(() => {
        this.loading = false
      })
    },
    // 搜索
    onSearch () {
      this.showEmpty = false
      this.pageNo = 1
      this.getSuitList(this.searchText, 1).then(() => {
        this.finished = false
      })
    },
    nextPage () {
      if (this.searchText && !this.showEmpty) {
        this.getSuitList(this.searchText, this.pageNo + 1)
      } else if (!this.isSearch) {
        this.getSuitList(this.searchText, this.pageNo + 1)
      } else {
        this.loading = false
      }
    },
    goDetail (item) {
      if (this.$util.isSgRn()) {
        window.postMessage(JSON.stringify({
          type: 'goNewWebview',
          data: {
            newUrl: `${config.HOST}/sgmobile/shopSuitDetail?storeId=${this.storeId}&packageId=${item.id}`,
          },
        }));
      } else {
        this.$router.push(`/shopSuitDetail?storeId=${this.storeId}&packageId=${item.id}`);
      }
    },
    back () {
      // this.$store.dispatch('doHistoryBack')
      this.isSearch = false
      this.getSuitList('', 1)
      this.finished = false
      this.searchText = ''

      document.querySelector('#app-warp').scrollTo(0, 0)
    },
    clear () {
      this.suitList = []
      this.showEmpty = false
      this.pageNo = 1
      this.finished = false
    },
    getSuitImage () {
      getSuitImage({ storeId: this.storeId }).then(res => {
        if (res.success && res.data) {
          this.shopInfo = res.data;
        }
      });
    },
    searchFocus () {
      this.$nextTick(() => {
        document.querySelectorAll("input[type='search']")[0].focus()
      })
      if (this.searchText) {
        return
      }
      this.isSearch = true
      this.suitList = []
      this.finished = false
      this.loading = false
      this.pageNo = 1
    },
    doShare () {
      let shareId = this.$route.query.shareId
      let url = location.href + `&shareId=${shareId || this.mId}`
      this.$util.inAppShare({
        title: '超值套购推荐',
        desc: '海尔智家商城，海尔家电正品保障',
        url
      })
    },
    WeChatSecondShare () {
      this.$store.dispatch('WeChatSecondShare', {
        title: '超值套购推荐',
        desc: '海尔智家商城，海尔家电正品保障'
      })
    }
  }
}
</script>
